.people {
  display: grid;
  gap: 16px;
  padding: 20px;
  max-width: 1500px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  color: #2F4F4F;

  @media (max-width: 749px) {
    grid-template-columns: repeat(2, 1fr); 
    margin: 30px 35px 155px;
  }

  @media (min-width: 750px) and (max-width: 1500px) {
    grid-template-columns: repeat(4, 1fr); 
    margin: 30px 305px 197px;

  }

  .person {
    position: relative;
    overflow: hidden;
    border-radius: 50%; 
    transition: all 0.5s ease;
    cursor: pointer;

    &:hover {
      background-color: #f0f0f0; 
      border-radius: 20px; 
      transform: scale(1.1); 
      transition: all 1s ease; 

      .photo img {
        border-radius: 10px; 
      }

      .details {
        opacity: 1; 
        transition: opacity 1s ease 1s; 
      }
    }

    &:not(:hover) {
      .photo img {
        transition: all 1s ease;
        border-radius: 50%; 
      }
    }

    .photo {
      position: relative;
      width: 100%;
      padding-top: 100%; 
      background: grey; 

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
        transition: all 0.5s ease;
      }

      .rank {
        position: absolute;
        background-color: white;
        border-radius: 50%;
        padding: 5px 10px;
        font-weight: bold;
        color: #8B0000;
        width: 9px;
        height: 18px;
        gap: 0px;
        opacity: 0px;
        display: flex; 
        align-items: center; 
        justify-content: center; 
        background-color: rgba(255, 255, 255, 0.8); 
        border-radius: 50%; 
        position: absolute; 
        top: 84px; 
        right: 10px; 
        font-size: 16px; 
      }
    }

    .details {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 10px;
      background-color: lightgrey; 
      color: white;
      opacity: 0; 
      transition: opacity 0.5s ease;
    }
    
      .name {
        display: block;
        text-align: center; 
        font-size: 20px;
        margin-top: 8px; 
        margin-bottom: 13px; 
        font-size: 18px; 
        color: #2F4F4F; 
        text-decoration: none; 
      }

    .extra {
      display: flex;
      justify-content: center;
      margin-top: 5px;
      color: #000000;

      .sport {
        font-size: 14px;
      }
    }
  }
  
}
